import React from "react";
import { Grid, Card, Image, Header, Divider, Icon } from "semantic-ui-react";
import port1 from "../asset/portfolio1.jpg";
import port2 from "../asset/portfolio2.jpg";
const img = [
  { value: port1, url: "dashboard", label: "Dashboard" },
  { value: port2, url: "covid-19", label: "Info Covid-19" }
];

const Portofolio = () => (
  <div>
    <Divider horizontal>
      <Header as="h4">
        <Icon name="qrcode" />
        Portofolio
      </Header>
    </Divider>
    <Grid container >
      <Grid.Row
        style={{
          fontSize: "1.3em",
          textAlign: "center"
        }}
      >
        {img.map(({ url, label, value }, i) => (
          <Grid.Column
            key={i}
            mobile={16}
            largeScreen={8}
            widescreen={8}
            tablet={8}
            computer={8}
          >
            <a href={`https://${url}.hary.fun`} target="blank">
              <Card centered style={{ marginBottom: "1em" }}>
                <Image size="big" src={value} wrapped ui={false} />
                <Card.Content>
                  <Card.Header>{label}</Card.Header>
                  <Card.Meta>{url}.hary.fun</Card.Meta>
                </Card.Content>
              </Card>
            </a>
          </Grid.Column>
        ))}
      </Grid.Row>
    </Grid>
  </div>
);

export default Portofolio;



